.layout-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(to bottom right, #eef2ff, #f8fafc);
  min-height: 100vh;
  padding: 40px 20px;
}
.intro-section {
  max-width: 1100px;
  text-align: center;
  margin-top: 40px;
}
.intro-header h1 {
  font-size: 2.2rem;
  color: #1e293b;
  font-weight: 700;
  margin-bottom: 10px;
}

.intro-header p {
  color: #475569;
  font-size: 1.1rem;
  line-height: 1.6;
  max-width: 700px;
  margin: 0 auto 40px;
}
.intro-card-container {
  display: flex;
  justify-content: center;
  gap: 25px;
  flex-wrap: wrap;
}

.intro-card {
  background-color: #ffffff;
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.1);
  border-radius: 14px;
  padding: 25px 20px;
  width: 250px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.intro-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 18px rgba(0, 0, 0, 0.15);
}

.intro-card h2 {
  color: #2563eb;
  font-size: 1.3rem;
  margin-bottom: 10px;
}

.intro-card p {
  color: #475569;
  font-size: 1rem;
  line-height: 1.5;
}
